export const formCalculadora = document.getElementById("form-calculadora");
const capitalAcumulado = document.getElementById("capital-acumulado");
const rentabilidadeDeInvestimentos = document.getElementById(
  "rentabilidade-de-investimentos"
);
const tempoAnos = document.getElementById("tempo-anos");
const aporteMensal = document.getElementById("aporte-mensal");
const telefoneCalculadora = document.getElementById("telefone-calculadora");
const nomeCalculadora = document.getElementById("nome-calculadora");
const valorCalculadora = document.getElementById("valorCalculadora");
export const SubmitCalculadora = document.getElementById("calculadora-submit");
export var inputsFields = [
  capitalAcumulado,
  rentabilidadeDeInvestimentos,
  tempoAnos,
  aporteMensal,
  telefoneCalculadora,
  nomeCalculadora,
];

export function checkIfEmpty(input) {
  if (input.value.length === 0) {
    return false;
  }
  return true;
}

export function checkSubmit() {
  let enviar = true;

  for (let i = 0; i < inputsFields.length; i++) {
    if (checkIfEmpty(inputsFields[i]) === false) {
      // console.log("Erro ao enviar");
      // console.log("Erro no input = " + i);
      inputsFields[i].previousSibling.style.color = "#990000";
      enviar = false;
    } else {
      inputsFields[i].previousSibling.style.color = "#000000";
    }
    // else {
    //   // console.log("Sucesso ao enviar");
    //   // console.log("Sucesso no input = " + i);
    // }
  }

  return enviar;
}

export function showpay() {
  if (tempoAnos.value < 0) {
    tempoAnos.value = 0;
  }
  var pv = capitalAcumulado.value;
  var meses = tempoAnos.value * 12;
  var rentabilidade = rentabilidadeDeInvestimentos.value / 100;
  var rentabilidade_mensal = Math.pow(1 + rentabilidade, 1 / 12) - 1;
  var aporte = aporteMensal.value;
  var juros = Math.pow(1 + rentabilidade_mensal, meses);

  valorCalculadora.innerText =
    "R$ " +
    Math.round(
      (pv * juros + (aporte * (juros - 1)) / rentabilidade_mensal) * 100
    ) /
      100;

  //document.calc.valor_final.value=1
}
